.footer{
    padding-top: 86px;
    padding-bottom: 82px;
}
.footer__row{
    display: flex;
    justify-content: space-between;
}
.footer__logo {
    margin-bottom: 15px;
}

.footer__info-text {
    margin-bottom: 40px;
    max-width: 300px;
}
.footer__socials {
    display: flex;
    column-gap: 30px;
    align-items: 30px;

        img:hover{
            opacity: 0.5;
        }
}
.footer__nav {
    display: flex;
    column-gap: 85px;

}
.footer__nav-block {
    font-size: 14px;
    li{
        margin-bottom: 13px;
    }
    a:hover{
        color: $accent-color;
    }

}
.footer__nav-block-title {
    font-size: 21px;
    font-weight: 700;
    margin-bottom: 18px;
}