.card-testimonials {

    border-radius: 5px;
    overflow: hidden;
    max-width: 350px;

    transition: box-shadow 0.2s ease-in;
    &:hover{
        box-shadow : 0px 10px 52px rgba(0, 0, 0, 0.08);
    }
}

.card-testimonial__img-weapper {
    position: relative;
}
.card-testimonial__btn-play {
    position: absolute;
    right: 20px;
    bottom: 20px;
    transform: translateY(50%);
    opacity: 0;
    transition: transform 0.2s ease-in, opacity 0.2s ease-in;

    .card-testimonials:hover &{
        transform: translateY(0%);
        opacity: 1;
    }
}

.card-testimonial__button-svg{
    circle{
        fill:#fff;
        transition: fill 0.2s ease-in;
    }

    path{
        fill:$accent-color;  
        transition: fill 0.2s ease-in;

    }

    &:hover{
        circle{
            fill:$accent-color;  
        }
    
        path{
            fill:#fff;  
    
        }
    }
}
.card-testimonials__body {
    padding: 30px;
}
.card-testimonials__text{
    margin-bottom: 23px;
}
.card-testimonials__name {
    font-size: 18px;
    font-weight: 700;
}
.card-testimonials__city {
    margin-top: 1px;
    color: #58BEBF;
    transition: color 0.2s ease-in;
    
    .card-testimonials:hover &{
        color: #F78434;
    }
}



